ks-order-dpd .content {
    background-color: #e8e8e8;

    position: relative;
    box-sizing: border-box;
    margin: 20px;
    max-width: 1200px;
    width: 100%;
    max-height: 100%;
    overflow-y: auto;
}

@media screen and (max-width: 640px) {
    ks-order-dpd .content {
        margin: 0px;
    }
}

ks-order-dpd > ks-button {
    max-width: 300px;
    margin: 15px auto 0 auto;
}

ks-order-dpd .topBar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #dc0032;
}

ks-order-dpd .missing {
    color: #dc0032;
}

ks-order-dpd .topBar ks-img2 {
    height: 40px;
}