import { Component, h, Prop, Element, State, Method } from '@stencil/core';

@Component({
  tag: 'ks-order-dpd',
  styleUrl: 'orderDPD.css'
})
export class OrderDPD {
    @Element() root: HTMLElement;
    @Prop() api: string = "";
    @Prop() button: string = "";
    @Prop() pointMessage: string = "";
    @Prop() missingMessage: string = "";
    @Prop() description: string = "";

    @State() code: string = "";
    @State() initialized = false;

    overlay: HTMLKsOverlayElement;
    componentDidLoad() {
        this.overlay = this.root.querySelector("ks-overlay");
    }

    show(event: Event) {
        event.preventDefault();
        this.overlay.show();
        this.initialized = true;
    }

    hide(event: Event) {
        event?.preventDefault();
        this.overlay.hide();
    }

    @State() valid: boolean = true;

    @Method()
    async Validate() {
        this.valid = this.code != "";
    }

    @Method()
    async IsValid() {
        return this.valid;
    }

    @Method()
    async select(point: string) {
        this.code = point;
        this.Validate();
        this.hide(null);

        let formData = new FormData();
            formData.append("rodzaj", "wysylka_dpdpickup");
            formData.append("koszyk", "0");
            formData.append("value", point);
            formData.append("punktodbioru", point);
        
        var headers = new Headers();
            headers.append('pragma', 'no-cache');
            headers.append('cache-control', 'no-cache');
        
        fetch(this.api, {
            method: 'POST', 
            headers: headers, 
            body: formData,
            credentials: "same-origin"
        });
    }

    render() {
        return [
            this.code ? <p>{this.pointMessage}: {this.code}</p> : null,
            this.valid === false ? <p class="missing">{this.missingMessage}</p> : null,

            <ks-button round icon="map-pin" onClick={(e)=>this.show(e)} name={this.button}/>,
                    
            <input type="hidden" name="lokalizacjaRuch" value={this.code}/>,
            <input type="hidden" name="ShippingDestinationCode" value={this.code}/>,

            <ks-overlay dark>
                <div class="content">
                    <div class="topBar">
                        <ks-img2 src="res/cart/dpd.svg" vertical height={100} width={300}/>
                        <ks-button icon="x" onClick={(e)=>this.hide(e)} />
                    </div>
                    
                    {this.initialized ? [
                        <script id="dpd-widget" type="text/javascript" innerHTML="
                            function pointSelected(pointID){
                                document.querySelector('ks-order-dpd').select(pointID);
                            }
                        "></script>,
                        <script type="text/javascript" src="//pudofinder.dpd.com.pl/source/dpd_widget.js?key=a9b08eb6c1b218b975529f9cea0e1364"></script>
                    ] : null}
                </div>
            </ks-overlay>
        ];
    }
}